html {
    section {
        break-inside: avoid;
    }
}

body {
    font: 11pt Georgia, "Times New Roman", Times, serif;
    line-height: 1.3;
    background: #fff !important;
    color: #000;
}

#job, #education, #summary, #knowlage {
    margin-block-start: 1.66em;
    margin-block-end: 0;
}

main {
    ul {
        margin-top: 0;
    }
}

h3 {
    margin: 20px 0 0 0;
    display: inline-block;
}

button {
    display: none;
}

.date {
    margin: 0 0 20px 0;
}

.info {

    figure {
        float: right;
        margin: 0;
        padding: 0;
        width: 30%;

        img {
            width: 100%;
        }
    }
}

.job {
    margin-bottom: 0.5em;
}

section {

    .job {
        margin: 0 0 20px 0;
    }

    h4 {
        margin: 0;
    }
}

.c-toggle {
    display: none;
}

.languangeFlag {
    display: none;
}

.hidePrint {
    display: none;
}

.information {
    margin: 0 0 17px 0;
}

@page {
    size: auto;
    /* auto is the initial value */
    margin: 0mm;
    /* this affects the margin in the printer settings */
}

@page :left {
    margin: 1cm
}

@page :right {
    margin: 1cm
}